var React = require("react");
var Radium = require("radium");
var Router = require("react-router");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;

var style = {
    color: "purple"
};

var noSubmissionsFound = React.createClass(Radium.wrap({
    displayName: "NoSubmissionsFound",
    mixins: [PureRenderMixin],

    render: function () {
        return (
            <div style={style}>Sorry, no submissions found. We should proably hide this link if you don't have any submissions.</div>
        );
    }
}));

module.exports = noSubmissionsFound;



