var React = require("react");
var Router = require("react-router");
var Submissions = require("./submissions/submissions");

var Route = Router.Route;
var DefaultRoute = Router.DefaultRoute;
var NotFoundRoute = Router.NotFoundRoute;

var defaultSubmissionsRoute = React.createClass({
    statics: {
        willTransitionTo(transition) {
            transition.redirect("submissions", {filter: "popular"});
        }
    },
    render() {
        return null;
    }
});

var routes = (
    <Route handler={Submissions}>
        <Route name="submissions" path="/submissions/:filter" handler={Submissions} />
        <DefaultRoute handler={defaultSubmissionsRoute}/>
    </Route>
);

module.exports = {
    run: function (el) {
        Router.run(routes, function (Handler, state) {
            React.render(<Handler params={state.params} />, el);
        });
    }
};



