var React = require("react");
var Router = require("react-router");
var Radium = require("radium");
var Filters = require("../consts/filters");
var Styles = require("../styles");
var RouteHandler = Router.RouteHandler;
var Link = Router.Link;

var filtersArray = Filters.getFilters();

var nav = React.createClass(Radium.wrap({
    displayName: "SubmissionsNav",
    propTypes: {
        disabled: React.PropTypes.bool.isRequired
    },

    getInitialState: function () {
        return null;
    },

    render: function () {
        var self = this;
        console.log("nav - render()");
        var navItems = filtersArray.map(function (filter) {
            if(self.props.disabled) {
                return (
                    <li key={filter.name}>{filter.title}</li>
                );
            }

            return (
                <li key={filter.name}>
                    <Link
                        to="submissions"
                        activeStyle={Styles.submissions.activeNavItem}
                        params={{filter: filter.name}}>{filter.title}</Link>
                </li>
            );
        });

        return (
            <ul>{navItems}</ul>
        );
    }
}));

module.exports = nav;



