var React = require("react");
var Router = require("react-router");
var DocumentTitle = require("react-document-title");
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var RouteHandler = Router.RouteHandler;
var Nav = require("./components/nav");
var Submission = require("./components/submission");
var NoSubmissionsFound = require("./components/noSubmissionsFound.jsx");
var Processing = require("./components/processing.jsx");
var Store = require("./stores/store");
var Actions = require("./actions/actions");
var Filters = require("./consts/filters");
var MidierCallStatuses = require("./consts/midtierCallStatuses");
var layzr = require("../layzr");

var Submissions = React.createClass({
    displayName: "Submissions",
    propTypes: {},
    contextTypes: {
        router: React.PropTypes.func
    },
    mixins: [Store.mixin, PureRenderMixin],

    storeDidChange: function () {
        this.setState(this.getState());
    },

    getInitialState: function () {
        return this.getState();
    },

    getState: function () {
        return {
            submissions: Store.submissions,
            submissionsFetchStatus: Store.submissionsFetchStatus
        };
    },

    componentDidMount: function () {
        this.changeFilterFromProps();
    },

    componentWillReceiveProps: function () {
        this.changeFilterFromProps();
    },

    getFilter: function () {
        return this.context.router.getCurrentParams().filter;
    },

    changeFilterFromProps: function () {
        var filter = this.getFilter();
        if (!filter) return;
        console.log("changeFilterFromProps() ", filter);
        Actions.getSubmissions(filter);
    },

    render: function () {
        console.log("submissions - render()");
        var processing = this.state.submissionsFetchStatus === MidierCallStatuses.processing;

        var submissions;
        if (processing)
            submissions = (<Processing />);

        if (!submissions && this.state.submissions.size === 0)
            submissions = (<NoSubmissionsFound />);

        if (!submissions) {
            submissions = this.state.submissions.map(function (submission) {
                return (
                    <Submission
                        key={submission.id}
                        caption={submission.caption}
                        imageSrc={submission.image_url}
                    />
                );
            });
            submissions = (<ul>{submissions}</ul>);
        }

        var title = "Submissions";
        var filter = this.getFilter();
        if (filter)
            title += ": " + Filters.getFilterByName(filter);

        return (
            <div>
                <DocumentTitle title={title}/>
                <Nav disabled={processing}/>
                {submissions}
            </div>
        );
    }
});

module.exports = Submissions;



